.sign-page-container {
    width: 50vw;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    .bttn-switch-container {
        margin: 5px 10px;
    }

    .sign-modal {

        display: flex;
        flex-direction: column;
        gap: 10px;


        .bttn-container {
            margin: 5px 10px;

            button {
                @include btn($type: dark);
            }
        }

        .input-file-container {
            margin: 5px 10px;

            input {
                display: none;
            }
        }

        .img-container-main {


            width: 100%;
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;

            .img-container {
                width: 200px;
                height: 200px;

                img {
                    opacity: 1;
                    visibility: visible;
                    border-radius: 50%;

                    position: relative;
                    display: block;
                    width: 100%;
                    height: 100%;
                    overflow: hidden;
                    transition: opacity .15s ease-out;
                    object-fit: cover;
                }
            }
        }
    }
}